#hamburger-icon {
  width: 30px;
  height: 22px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 8px;
  text-align: right;
  z-index: 6;
  right: 15px;
  top: 30px;
  transition: top 0.2s ease-in-out;
}
#hamburger-icon.active .line {
  background: #212121;
}
#hamburger-icon .line {
  display: block;
  background: #212121;
  width: 30px;
  height: 2px;
  right: 0;
  border-radius: 1px;
  transition: all 0.4s;
}
#hamburger-icon .line.line-1 {
  width: 75%;
  top: 0;
}
#hamburger-icon .line.line-2 {
  top: 50%;
}
#hamburger-icon .line.line-3 {
  top: 100%;
}
#hamburger-icon.active .line {
  background: #212121;
  color: #212121;
}
#hamburger-icon.active .line-1 {
  transform: translateY(10px) translateX(0) rotate(45deg);
  width: 100%;
}
#hamburger-icon.active .line-2 {
  opacity: 0;
  width: 100%;
}
#hamburger-icon.active .line-3 {
  transform: translateY(-10px) translateX(0) rotate(-45deg);
  width: 100%;
}

